import React, { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import { getUserData, updateUserData } from "../services/firebase";

const CountMoneyPage = () => {

    const user = useSelector((state) => state.auth.user); // Получаем состояние пользователя
    // Состояние для хранения введенной суммы
    const [inputMoney, setInputMoney] = useState('');
    const [moneyDivider, setMoneyDivider] = useState('');
    const [moneyDividerDisplay, setMoneyDividerDisplay] = useState('');
    const [isLoading, setIsLoading] = useState(false);

    // const dividerRef = useRef(null);

    // Состояние для хранения суммы, используемой для генерации блоков
    const [displayedMoney, setDisplayedMoney] = useState('');

    // Состояние для выбора режима отображения
    // const [displayMode, setDisplayMode] = useState("mode1"); // "mode1" или "mode2"

    // Функция для обработки ввода суммы
    const handleInputChange = (e) => {
        const value = e.target.value;
        if (!isNaN(value)) {
            setInputMoney(Number(value)); // Преобразуем в число
        }
    };
    // Функция для обработки ввода суммы
    const handleDivderChangeDisplay = (e) => {
        const value = e.target.value;
        if (!isNaN(value)) {
            setMoneyDividerDisplay(Number(value)); // Преобразуем в число
        }
    };

    // Функция для подтверждения ввода суммы
    const confirmMoney = () => {
        setDisplayedMoney(inputMoney); // Устанавливаем сумму для отображения
        submit_money_firebase_request()
        setMoneyDivider(moneyDividerDisplay)
    };



    // Генерация блоков для режима 2
    const generateBlocksMode2 = () => {

        // const divider = 5000
        // const divider = Number(moneyDividerDislpay) > 0 ? Number(moneyDividerDislpay) : 5000
        const divider = Number(moneyDividerDisplay) > 0 ? Number(moneyDivider) : 5000

        let textDividerDisplay = 0

        if (divider > 1000) {
            if (divider % 1000 === 0) {
                textDividerDisplay = ((divider / 1000).toFixed(0) + 'K')
            }
            else if (divider % 100 === 0) {
                textDividerDisplay = ((divider / 1000).toFixed(1) + 'K')

            }
            else if (divider % 10 === 0) {
                textDividerDisplay = divider

            }
            else if (divider % 1 === 0) {
                textDividerDisplay = divider

            }
            else {
                textDividerDisplay = ((divider / 1000).toFixed(1) + 'K')

            }
        }
        else {
            textDividerDisplay = divider
        }




        const blocks = [];
        const blockAmount = Math.ceil(displayedMoney / divider); // Количество блоков по 5000
        const remainder = displayedMoney % divider;


        // let textRemaindexDisplay = remainder > 1000 ? ((remainder / 1000).toFixed(2) + 'K') : remainder
        let textRemaindexDisplay = 0


        // let textDividerDisplay = 0

        if (remainder > 1000) {
            if (remainder % 1000 === 0) {
                textRemaindexDisplay = ((remainder / 1000).toFixed(0) + 'K')
            }
            else if (remainder % 100 === 0) {
                textRemaindexDisplay = ((remainder / 1000).toFixed(1) + 'K')

            }
            else if (remainder % 10 === 0) {
                textRemaindexDisplay = remainder

            }
            else if (remainder % 1 === 0) {
                textRemaindexDisplay = remainder

            }
            else {
                textRemaindexDisplay = ((remainder / 1000).toFixed(1) + 'K')

            }
        }
        else {
            textRemaindexDisplay = remainder
        }


        const widthPercent = ((remainder / divider).toFixed(3) * 100)

        // console.log('widthPixels', widthPixels);

        let indexGenerate = 1
        if (remainder === 0) {
            indexGenerate = 0
        }

        // Генерация полных блоков
        for (let i = indexGenerate; i < blockAmount; i++) {
            blocks.push(
                <div
                    key={i}
                    style={{
                        width: "100%",
                        height: "50px",
                        backgroundColor: "#00ff15",
                        // margin: "2px",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        color: "black",
                        fontWeight: "bold",
                        border: '1px solid rgba(255, 255, 255, 0.37)',
                    }}
                >
                    {/* {divider} */}
                    {textDividerDisplay}
                </div>
            );
        }

        // Добавляем блок с остатком, если он есть
        if (remainder > 0) {
            blocks.push(
                <div style={{
                    // width: "100px",
                    width: "100%",
                    height: "50px",
                    // margin: "2px",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "flex-start",
                    border: '1px solid rgba(255, 255, 255, 0.37)',
                    backgroundColor: "#000",
                    position: "relative" // Добавляем относительное позиционирование для родителя
                }}>
                    <div
                        key="remainder"
                        style={{
                            width: `${widthPercent}%`, // Заполнение по проценту от 5000
                            height: "50px",
                            backgroundColor: "#00ff15",
                            position: "absolute", // Абсолютное позиционирование
                            left: 0, // Выравниваем к левому краю
                            top: 0, // Выравниваем к верху
                        }}
                    ></div>

                    <p style={{
                        position: "absolute", // Абсолютное позиционирование
                        width: "100%", // Текст будет по всей ширине блока
                        height: "100%", // Текст по всей высоте
                        display: "flex", // Используем flex для центрирования
                        alignItems: "center", // Центрируем по вертикали
                        justifyContent: "center", // Центрируем по горизонтали
                        zIndex: 2, // Поверх закраски
                    }}>
                        <span style={{
                            color: "black",
                            padding: '5px',
                            fontWeight: "bold",
                            backgroundColor: 'rgba(93, 255, 107, 0.671)'

                        }}>
                            {/* {remainder} */}
                            {textRemaindexDisplay}

                        </span>
                    </p>
                </div>
            );
        }

        blocks.push(
            <div
                // key={i}
                style={{
                    // width: "100px",
                    width: "100%",
                    height: "50px",
                    backgroundColor: "#000",
                    // margin: "2px",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    color: "black",
                    fontWeight: "bold",
                    border: '1px solid rgba(255, 255, 255, 0.37)'
                }}
            >
                {/* {divider} ₽ */}
            </div>
        );




        return blocks;
    };


    const submit_money_firebase_request = async () => {
        try {
            // const dateNow = Date.now()
            const moneyObject = {
                sum: inputMoney,
                divider: moneyDividerDisplay

            }
            setIsLoading(true);
            await updateUserData(user.uid, 'money_count', moneyObject); // Вызываем функцию для обновления данных
            setIsLoading(false);
            // set_nofap_timestamp(dateNow)
        } catch (error) {
            console.log('Ошибка при обновлении базы данных, не получилось начать таймер....', error);

        }
    };


    useEffect(() => {
        const fetchUserData = async () => {
            try {
                setIsLoading(true);
                const data = await getUserData(user.uid); // Вызываем функцию с UID пользователя
                // console.log('data', data);

                setInputMoney(data.money_count.sum)
                setDisplayedMoney(data.money_count.sum)

                const responseMoneyDivier = data.money_count.divider ? data.money_count.divider : 5000

                console.log('divier из базы данных:', responseMoneyDivier);


                setMoneyDividerDisplay(responseMoneyDivier)
                setMoneyDivider(responseMoneyDivier)
                // setMoneyDividerDisplay(data.money_count.divider ? data.money_count.divider : 5000)
                // setMoneyDivider(5000)
                setIsLoading(false)
            } catch (err) {
                setIsLoading(false)
                console.log('Ошибка при загрузке данных из базы данных, nofap_timestamp не получен..', err);

                // setError(err.message);
            } finally {
                setIsLoading(false);
            }
        };

        fetchUserData();
    }, []);


    if (isLoading) {
        return <div><p>Загрузка из базы данных... подождите...</p></div>
    }
    else {

        return (
            <div style={{ padding: "20px", fontFamily: "Arial, sans-serif" }}>
                {/* <p>moneyDividerDislpay: {moneyDividerDisplay}</p>
                <p>moneyDivier: {moneyDivider}</p>
                <p>inputMoney: {inputMoney}</p> */}
                <h1>Счетчик денег</h1>

                <div>

                    <div
                    // style={{ marginBottom: "20px" }}
                    >
                        <span>
                            Введите сумму денег:

                        </span>
                        <input
                            value={inputMoney}
                            onChange={handleInputChange}
                            style={{ marginLeft: "10px", padding: "5px" }}
                        />





                    </div>
                    <div
                    // style={{ marginBottom: "20px" }}
                    >
                        <span>
                            Введите делитель:

                        </span>

                        <input
                            // ref={dividerRef}
                            value={moneyDividerDisplay}
                            // onChange={setMoneyDividerDisplay}
                            onChange={

                                handleDivderChangeDisplay

                            }
                            style={{ marginLeft: "10px", padding: "5px" }}
                        />



                        <button onClick={confirmMoney} style={{ marginLeft: "10px", padding: "5px 10px" }}>
                            Подтвердить
                        </button>

                    </div>
                </div>




                {/* Отображение текущей суммы */}
                <p>
                    Текущая сумма: <strong style={{
                        color: "#00ff15",
                    }}>{displayedMoney}</strong> ₽
                </p>
                <p>
                    Ты можешь заработать еще <strong style={{
                        color: "#00ff15",
                    }}>{moneyDivider - (displayedMoney % moneyDivider)}</strong> ?
                </p>

                {/* Таблица блоков */}
                <div
                    style={{
                        display: "grid",
                        // gridTemplateColumns: "repeat(auto-fill, minmax(100px, 1fr))", // Адаптивная сетка
                        gridTemplateColumns: "repeat(7,1fr)", // Адаптивная сетка
                        width: "100%",
                        maxWidth: '800px',

                        gap: "10px",
                        marginTop: "20px",
                    }}
                >
                    {/* {displayMode === "mode1" ? generateBlocksMode1() : generateBlocksMode2()} */}
                    {generateBlocksMode2()}
                </div>
            </div>
        );
    }


};

export default CountMoneyPage;