// src/components/Login.js
import React from "react";
import { useDispatch } from "react-redux";
import { loginStart, loginSuccess, loginFailure } from "../redux/authSlice";
import { auth, provider, signInWithPopup, db } from "../services/firebase";
import { doc, setDoc, getDoc } from "firebase/firestore";

const Login = () => {
    const dispatch = useDispatch();

    const handleGoogleLogin = async () => {
        console.log('попытка залогиниться...');
        dispatch(loginStart());
        try {
            const result = await signInWithPopup(auth, provider);
            console.log('result', result);

            // Извлекаем только нужные данные
            const user = {
                uid: result.user.uid,
                displayName: result.user.displayName,
                email: result.user.email,
                photoURL: result.user.photoURL,
            };

            // Проверяем, существует ли пользователь в базе данных
            const userRef = doc(db, "users", user.uid);
            console.log('userRef', userRef);

            const userDoc = await getDoc(userRef);
            console.log('userDoc', userDoc);


            if (!userDoc.exists()) {
                // Если пользователя нет, создаем новый документ с начальными данными
                try {
                    await setDoc(userRef, {
                        displayName: user.displayName,
                        email: user.email,
                        photoURL: user.photoURL,
                        nofap_timestamp: '0',
                        nofap_links: [
                            {
                                link: 'https://drive.google.com/file/d/1pwPKr-wq0yw-ZTyHwGxOyqOit9_BXXEV/view?usp=drive_link', note: 'почитай гайд на сексуальную энергию'
                            },
                            {
                                link: 'https://www.youtube.com/watch?v=CwGQiTCrqEA&list=PLB5lKOLFXiEfstpsfl7UcdvwBV6TpqG7G&pp=gAQB', note: 'отвлекись, посмотри видео'
                            },
                        ]

                    });
                } catch (error) {
                    console.log('error is', error);

                }

            }

            dispatch(loginSuccess(user)); // Сохраняем только сериализуемые данные
        } catch (error) {
            console.error('Ошибка входа:', error.message);
            dispatch(loginFailure(error.message));
        }
    };

    return (
        <div
            style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100vh",
            }}
        >
            <button onClick={handleGoogleLogin} style={{ padding: "10px 20px" }}>
                Войти через Google
            </button>
        </div>
    );
};

export default Login;