const app = initializeApp(firebaseConfig);
const auth = getAuth(app); // Инициализируем аутентификацию
const provider = new GoogleAuthProvider(); // Создаем провайдера Google
const db = getFirestore(app);
export const listenToAuthChanges = (callback) => {
    onAuthStateChanged(auth, (user) => {
        callback(user); // Передаем текущего пользователя в callback
    });
};


export const getUserData = async (uid) => {
    try {
        if (!uid) {
            throw new Error("UUID пользователя не указан");
        }

        // Создаем ссылку на документ пользователя в коллекции "users"
        const userRef = doc(db, "users", uid);

        // Получаем документ пользователя
        const userDoc = await getDoc(userRef);

        // Если документ существует, возвращаем его данные
        if (userDoc.exists()) {
            console.log("Данные пользователя:", userDoc.data());
            return userDoc.data();
        } else {
            console.log("Пользователь не найден");
            return null;
        }
    } catch (error) {
        console.error("Ошибка при получении данных пользователя:", error.message);
        throw error; // Передаем ошибку дальше
    }
};



export const updateUserData = async (userId, fieldName, value) => {
    try {
        console.log(userId, fieldName, value);

        // Создаем ссылку на документ пользователя в коллекции "users"
        const userRef = doc(db, "users", userId);

        // Обновляем или создаем поле в документе пользователя
        await setDoc(
            userRef,
            { [fieldName]: value }, // Динамическое имя поля
            { merge: true } // Если документ существует, он будет обновлен, а не перезаписан
        );

        console.log(`Поле "${fieldName}" успешно обновлено для пользователя ${userId}`);
    } catch (error) {
        console.error("Ошибка при обновлении данных пользователя:", error.message);
        throw error; // Передаем ошибку дальше
    }
};


export const addLinkToUser_nofap = async (userId, linkData) => {
    try {
        // Создаем ссылку на документ пользователя в коллекции "users"
        const userRef = doc(db, "users", userId);

        // Добавляем объект в массив nofap_links
        await updateDoc(userRef, {
            nofap_links: arrayUnion(linkData), // Добавляем новый объект в массив
        });

        console.log(`Ссылка успешно добавлена для пользователя ${userId}`);
    } catch (error) {
        console.error("Ошибка при добавлении ссылки:", error.message);
        throw error; // Передаем ошибку дальше
    }
};


export const removeLinkFromUser_nofap = async (userId, linkData) => {
    try {
        // Создаем ссылку на документ пользователя в коллекции "users"
        const userRef = doc(db, "users", userId);

        // Удаляем объект из массива nofap_links
        await updateDoc(userRef, {
            nofap_links: arrayRemove(linkData), // Удаляем указанный объект из массива
        });

        console.log(`Ссылка успешно удалена для пользователя ${userId}`);
    } catch (error) {
        console.error("Ошибка при удалении ссылки:", error.message);
        throw error; // Передаем ошибку дальше
    }
};

