import React from "react";
import { BrowserRouter, Routes, Route, Navigate, Link } from "react-router-dom";
import { useSelector } from "react-redux";

// Компоненты
import Login from "../components/Login";
import NoFapPage from "../pages/NoFap__page";
import CountMoneyPage from "../pages/CountMoney__page";
import { getUserData } from "../services/firebase";
import Logout from "../pages/Logout__page";

// Header с динамическими ссылками
const Header = ({ routes }) => {
    const user = useSelector((state) => state.auth.user); // Получаем состояние пользователя



    // Фильтруем маршруты до рендеринга
    const visibleRoutes = routes.filter(
        (route) => !route.isPrivate || user
    );


    React.useEffect(() => {
        const fetchUserData = async () => {
            try {
                // setLoading(true);
                const data = await getUserData(user.uid); // Вызываем функцию с UID пользователя
                console.log('data', data);

                // setUserData(data);
            } catch (err) {
                // setError(err.message);
            } finally {
                // setLoading(false);
            }
        };

        fetchUserData();
    }, []);

    return (
        <header style={{ padding: "10px", backgroundColor: "#000000" }}>
            {/* <div style={{ marginBottom: "10px" }}>
                <h1>Мое приложение</h1>
            </div> */}
            {!user ? <div>
                <p>
                    Вы не в системе... войдите...
                </p>

            </div> : <div>
                <p>Вы в системе 😀 {user.email}</p>
            </div>}

            {/* Информация о пользователе */}
            {/* {user && (
                <div style={{ marginBottom: "10px" }}>
                    <p><strong>Email:</strong> {user.email}</p>
                    <p><strong>Имя:</strong> {user.displayName}</p>
                    <p><strong>uid:</strong> {user.uid}</p>
                    <p><strong>Дата начала воздержания:</strong> { }</p>

                    {user.photoURL && (
                        <img
                            src={user.photoURL}
                            alt="Avatar"
                            style={{ width: "50px", borderRadius: "50%" }}
                        />
                    )}
                </div>
            )} */}

            {/* Навигация */}
            <nav>
                <div style={{ display: "flex", gap: "10px" }}>
                    {/* <div>
                        login_state: {user?.email}
                    </div> */}
                    {visibleRoutes.map((route, index) => {
                        if (user && route.isVisibleWhenLoggined === true) {
                            return (
                                <div key={index}>
                                    <Link
                                        to={route.path}
                                        style={{
                                            textDecoration: "none",
                                            color: "blue",
                                            padding: "5px 10px",
                                            border: "1px solid #ccc",
                                            borderRadius: "5px",
                                            backgroundColor: "#fff",
                                        }}
                                    >
                                        {route.label || route.path}
                                    </Link>
                                </div>

                            )
                        }
                        else if (!user && route.isVisibleWhenLoggined === false) {
                            return (
                                <div key={index}>
                                    <Link
                                        to={route.path}
                                        style={{
                                            textDecoration: "none",
                                            color: "blue",
                                            padding: "5px 10px",
                                            border: "1px solid #ccc",
                                            borderRadius: "5px",
                                            backgroundColor: "#fff",
                                        }}
                                    >
                                        {route.label || route.path}
                                    </Link>
                                </div>

                            )
                        }
                        else {
                            return (
                                <div key={index}>
                                    {/* <Link
                                        to={route.path}
                                        style={{
                                            textDecoration: "none",
                                            color: "blue",
                                            padding: "5px 10px",
                                            border: "1px solid #ccc",
                                            borderRadius: "5px",
                                            backgroundColor: "#fff",
                                        }}
                                    >
                                        {route.label || route.path}
                                    </Link> */}
                                </div>

                            )
                        }


                    }
                    )}
                </div>
            </nav>
        </header>
    );
};

// Конфигурация маршрутов
const routeConfig = [
    {
        path: "/login",
        element: <Login />,
        isPrivate: false, // Доступен без авторизации
        isVisibleWhenLoggined: false,
        label: "Вход", // Отображаемое название в Header
    },
    {
        path: "/logout",
        element: <Logout />,
        isPrivate: true, // Доступен без авторизации
        isVisibleWhenLoggined: true,
        label: "Выход", // Отображаемое название в Header
    },

    {
        path: "/nofap_page",
        element: <NoFapPage />,
        isPrivate: true, // Требует авторизации
        isVisibleWhenLoggined: true,
        label: "Трекер воздержания",
    },

    {
        path: "/countmoney_page",
        element: <CountMoneyPage />,
        isPrivate: true, // Требует авторизации
        isVisibleWhenLoggined: true,
        label: "Трекер денег",
    },
    {
        path: "*",
        element: <Navigate to="/login" />, // Перенаправление на страницу входа
        isPrivate: false,
    },
];



const PrivateRoute = ({ children, isPrivate }) => {
    const user = useSelector((state) => state.auth.user);
    return isPrivate ? (user ? children : <Navigate to="/login" />) : children;
};

// Основной компонент AppRoutes
const AppRoutes = () => {
    return (
        <BrowserRouter>
            {/* Header с динамическими ссылками */}
            <Header routes={routeConfig.filter((route) => route.label)} />

            {/* Роутинг */}
            <Routes>
                {routeConfig.map((route, index) => (
                    <>
                        <Route
                            key={index}
                            path={route.path}
                            element={
                                <PrivateRoute isPrivate={route.isPrivate}>

                                    {route.element}
                                </PrivateRoute>
                            }
                        />
                    </>
                ))}
            </Routes>
        </BrowserRouter>
    );
};

export default AppRoutes;