import React, { useEffect, useState } from "react";
import { addLinkToUser_nofap, getUserData, removeLinkFromUser_nofap, updateUserData } from "../services/firebase";
import { useSelector } from "react-redux";


const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp);
    const day = date.getDate();
    const month = date.toLocaleString("ru", { month: "long" }); // Месяц в родительном падеже
    const year = date.getFullYear();
    return `${day} ${month} ${year}`;
};


const calculateTimeDifference = (timestamp) => {
    const now = Date.now(); // Текущее время в миллисекундах
    const difference = Math.abs(now - timestamp); // Разница в миллисекундах

    // Конвертируем миллисекунды в дни, часы, минуты и секунды
    const days = Math.floor(difference / (1000 * 60 * 60 * 24));
    const hours = Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    const minutes = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((difference % (1000 * 60)) / 1000);

    // Формируем строку с разницей
    return `${days}д ${hours}ч ${minutes}м ${seconds}с`;
};
const ProgressBar = ({ timestamp }) => {
    // Функция для расчета количества полных и неполных дней
    const calculateProgressBoxes = (timestamp) => {
        const now = Date.now(); // Текущее время в миллисекундах
        const difference = Math.abs(now - timestamp); // Разница в миллисекундах

        // Полные дни
        const fullDays = Math.floor(difference / (1000 * 60 * 60 * 24));

        // Остаток времени в днях (дробная часть)
        const remainingTime = (difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60 * 24);

        return { fullDays, remainingTime };
    };

    const { fullDays, remainingTime } = calculateProgressBoxes(timestamp);

    // Генерация квадратиков
    const totalBoxes = fullDays + (remainingTime > 0 ? 1 : 0); // Общее количество квадратиков
    const boxes = Array.from({ length: totalBoxes }, (_, index) => {
        if (index < fullDays) {
            return { filled: 1 }; // Полностью закрашенный квадрат
        } else {
            return { filled: remainingTime }; // Частично закрашенный квадрат
        }
    });

    return (
        <div className="progressBar_noFap">

            <div style={{
                display: 'grid',
                // justifyContent: 'flex-start'
                gridTemplateColumns: '1fr 1fr',
                lineHeight: '50px,'

            }}>
                <p



                >
                    Дата начала:<br />
                    <span style={{
                        lineHeight: '50px'
                    }} className="light_green" > {formatTimestamp(timestamp)}</span>
                </p>

                <p>
                    Я не додик-омежка-дрочила уже: <br />
                    <span style={{
                        lineHeight: '50px'
                    }} className="light_green">{calculateTimeDifference(timestamp)}</span>
                </p>

            </div>

            <div className="progressBar_green_boxes_wrap">
                {boxes.map((box, index) => (
                    <div className="progressBar_green_box_wrap">
                        <div
                            key={index}
                            className="progressBar_green_box"
                            style={{
                                // width: "100px",
                                // height: "20px",
                                // margin: "2px",
                                background: box.filled === 1
                                    ? "#00ff15" // Полностью закрашенный квадрат
                                    : `linear-gradient(to right, #00ff15 ${box.filled * 100}%, transparent ${box.filled * 100}%)`, // Частично закрашенный квадрат
                            }}
                        ></div>

                    </div>
                ))}

                <div className="progressBar_green_box_wrap">
                    <div

                        className="progressBar_green_box"
                        style={{
                            // width: "100px",
                            // height: "20px",
                            // margin: "2px",
                            // background: 'transparent'
                        }}
                    ></div>

                </div>
            </div>
        </div>
    );
};


const NoFapPage = () => {

    const user = useSelector((state) => state.auth.user); // Получаем состояние пользователя




    // Состояния для управления данными
    const [nofap_timestamp, set_nofap_timestamp] = useState(0)
    const [isLoading, setIsLoading] = useState(true)

    const [startDate, setStartDate] = useState("");
    const [startTime, setStartTime] = useState("");
    const [isAbstaining, setIsAbstaining] = useState(false);
    const [links, setLinks] = useState([]);
    const [newLink, setNewLink] = useState("");
    const [newLinkNote, setNewLinkNote] = useState("");



    useEffect(() => {
        const fetchUserData = async () => {
            try {
                // setLoading(true);
                const data = await getUserData(user.uid); // Вызываем функцию с UID пользователя
                // setUserData(data);
                console.log('data', data);

                set_nofap_timestamp(data.nofap_timestamp)
                if (data.nofap_links) {
                    setLinks(data.nofap_links)

                }
                setIsLoading(false)
            } catch (err) {
                setIsLoading(false)
                console.log('Ошибка при загрузке данных из базы данных, nofap_timestamp не получен..', err);

                // setError(err.message);
            } finally {
                // setLoading(false);
            }
        };

        fetchUserData();
    }, [isLoading]);


    const handleAddLink = () => {
        let sendItem = { "link": newLink, "note": newLinkNote }

        setIsLoading(true)

        addLinkToUser_nofap(user.uid, sendItem)
            .then(() => {
                console.log("Ссылка успешно добавлена!");
                setIsLoading(false)
                setNewLink('')
                setNewLinkNote('')
            })
            .catch((error) => {
                console.error("Ошибка при добавлении ссылки:", error.message);
                setIsLoading(false)
                setNewLink('')
                setNewLinkNote('')
            });



    };


    const handleRemoveLink = (linkToRemove) => {
        setIsLoading(true)

        removeLinkFromUser_nofap(user.uid, linkToRemove)
            .then(() => {
                console.log("Ссылка успешно удалена!");
                setIsLoading(false)
            })
            .catch((error) => {
                console.error("Ошибка при удалении ссылки:", error.message);
                setIsLoading(false)
            });
    };




    const start_nofap_now_function = async () => {
        try {
            const dateNow = Date.now()
            await updateUserData(user.uid, 'nofap_timestamp', dateNow); // Вызываем функцию для обновления данных
            set_nofap_timestamp(dateNow)
        } catch (error) {
            console.log('Ошибка при обновлении базы данных, не получилось начать таймер....', error);

        }
    };





    const start_nofap_fromdate_function = async () => {
        const createTimestamp = (date, time) => {
            const [hours, minutes] = time.split(":"); // Разбиваем время на часы и минуты
            const dateObj = new Date(date); // Создаем объект Date из строки даты
            dateObj.setHours(hours); // Устанавливаем часы
            dateObj.setMinutes(minutes); // Устанавливаем минуты
            return dateObj.getTime(); // Возвращаем timestamp (в миллисекундах)
        };


        try {
            const timestamp = createTimestamp(startDate, startTime);
            console.log("Timestamp:", timestamp); // Например: 1746432120000
            await updateUserData(user.uid, 'nofap_timestamp', timestamp); // Вызываем функцию для обновления данных
            set_nofap_timestamp(timestamp)
        } catch (error) {
            console.log('Ошибка при обновлении базы данных, не получилось начать таймер....', error);

        }
    };


    const wipe_nofap_now_function = async () => {
        try {
            await updateUserData(user.uid, 'nofap_timestamp', 0); // Вызываем функцию для обновления данных
            set_nofap_timestamp(0)
        } catch (error) {
            console.log('Ошибка при обновлении базы данных, не получилось сбросить таймер....', error);
        }
    };


    if (isLoading) {
        return <div><p>Загрузка из базы данных... подождите...</p></div>
    }
    else {

        return (
            <div style={{ padding: "20px", fontFamily: "Arial, sans-serif" }}>
                <div
                    className="nofap_container"

                >
                    <div>

                        <h1>Трекер воздержания</h1>


                        {nofap_timestamp > 0 ?
                            <>





                                <ProgressBar timestamp={nofap_timestamp} />

                                {/* Кнопка для отметки нарушения */}
                                <div style={{ marginBottom: "20px" }}>
                                    <button
                                        onClick={wipe_nofap_now_function}
                                        style={{
                                            marginTop: '50px',
                                            cursor: "pointer",
                                        }}
                                    >
                                        Подрочил / Посмотрел порно
                                    </button>
                                </div>
                            </>
                            :
                            <>
                                <div style={{ marginBottom: "20px" }}>
                                    <h2>Начать воздержание</h2>
                                    <div style={{ marginBottom: "10px" }}>
                                        <label>
                                            Дата начала:
                                            <input
                                                type="date"
                                                value={startDate}
                                                onChange={(e) => setStartDate(e.target.value)}
                                                style={{ marginLeft: "10px" }}
                                            />
                                        </label>
                                    </div>
                                    <div style={{ marginBottom: "10px" }}>
                                        <label>
                                            Время начала:
                                            <input
                                                type="time"
                                                value={startTime}
                                                onChange={(e) => setStartTime(e.target.value)}
                                                style={{ marginLeft: "10px" }}
                                            />
                                        </label>
                                    </div>

                                    <button onClick={start_nofap_now_function} style={{ padding: "5px 10px" }}>
                                        Начать воздержание сейчас
                                    </button>
                                    <button
                                        onClick={start_nofap_fromdate_function}
                                        style={{ padding: "5px 10px", marginLeft: "10px" }}
                                    >
                                        Уже воздерживаюсь
                                    </button>
                                </div>
                            </>
                        }
                    </div>

                    {/* Раздел для сохранения ссылок */}
                    <div style={{ marginBottom: '50px', }}>
                        <h2>Сохранённые ссылки</h2>
                        <div style={{ marginBottom: "10px" }}>
                            <input
                                type="text"
                                placeholder="Введите ссылку"
                                value={newLink}
                                onChange={(e) => setNewLink(e.target.value)}
                                style={{ marginRight: "10px", padding: "5px" }}
                            />
                            <input
                                type="text"
                                placeholder="Введите описание"
                                value={newLinkNote}
                                onChange={(e) => setNewLinkNote(e.target.value)}
                                style={{ marginRight: "10px", padding: "5px" }}
                            />
                            <button onClick={handleAddLink} style={{ padding: "5px 10px" }}>
                                Сохранить
                            </button>
                        </div>
                        {/* <ul> */}
                        {links.map((link, index) => (
                            // <li >
                            <div key={index} className="nofap_link">
                                <a href={link.link} target="_blank" rel="noopener noreferrer">
                                    {link.note}
                                    {/* хуйня */}
                                </a>
                                <button onClick={() =>
                                    handleRemoveLink(link)
                                }>
                                    Удалить
                                </button>

                            </div>
                            // </li>
                        ))}
                        {/* </ul> */}
                    </div>
                </div>



            </div>
        );
    }

};

export default NoFapPage;